<template>
  <div class="staticReports">
    <el-container>
      <el-header>
        <div class="staticReports minWidth loadingtext"
            v-loading="imgLoading"
            element-loading-text="加载中..."
            element-loading-background="rgba(255, 255, 255, 0.8)">
            <div class="taticReports-content">
              <el-row class="staticReports-header" type="flex" justify="space-between">
                <el-col :span="17" class="titleMT"><span class="mz" @click="$router.back(-1)">流程报表</span><i :class="iconTwo"></i>
                </el-col>
                <el-col :span="6" class="reportInstancebtns">
                  <el-button @click="search" type="primary"  round  size="mini" >搜索</el-button>
                  <el-button @click="reset" type="primary" round  size="mini" >重置</el-button>
                </el-col>
              </el-row>
            <br>
            <!--<hr>-->
              <div class="statement reportCondition" >
                <span class="fz16">动态-以“步骤名称”统计实例最多的流程</span>
                <el-row class="staticReports-middle lh35 staticReportsMiddle">
                  <el-col :span="2" :sm="4" :md="1" class="classSelect"><span>流程分类选择</span></el-col>
                  <el-col :span="7" class="classSelectMR">
                  <span @dblclick="openDailog('processClassification')">
                    <i class="iconfont icon-organization" @click="openDailog('processClassification')"></i>
                    <el-input class="input-text" disabled  v-model="processCategoryName" :title="processCategoryName"></el-input>
                  </span>
                  </el-col>
                  <!--<el-col :span="1"></el-col>-->

                  <el-col :span="2" :sm="4" :md="1" style="min-width: 78px;margin-left: 30px" ><span>流程选择</span></el-col>
                  <el-col :span="6" class="classSelectMR">
                  <span @dblclick="openDailog('processSelection')">
                    <i class="iconfont icon-organization pl18"  @click="openDailog('processSelection')"></i>
                    <el-input class="input-text" disabled  v-model="processName" :title="processName"></el-input>
                  </span>
                  </el-col>
                  <!--<el-col :span="1" style="text-indent: 6px"><i class="iconfont icon-organization pl18"  @click="openDailog('processSelection')"></i></el-col>-->
                </el-row>

                <el-row class="reportCondition-date reportConditionDate">
                  <el-col  :span="2" :sm="4" :md="1" class="timeDuan"><span>时间段</span></el-col>
                  <el-col :span="8" :sm="8" class="timeSelect">
                    <el-radio-group v-model="selectValue" @change="change">
                      <el-radio :label="1">本周</el-radio>
                      <el-radio :label="2">本月</el-radio>
                      <el-radio :label="3">本季度</el-radio>
                      <el-radio :label="4">本年</el-radio>
                      <el-radio :label="5">其他</el-radio>
                    </el-radio-group>
                  </el-col>
                  <!--<el-col :span="2" :sm="4" :md="1" class="space"><span>&nbsp;</span></el-col>-->
                  <el-col :span="6" :sm="6"  v-if="selectValue===5">
                    <div class="border">
                      <el-date-picker
                        class="timeWidth"
                        v-model="model.date"
                        type="daterange"
                        value-format="yyyy-MM-dd"
                        range-separator="~"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                      </el-date-picker>
                    </div>
                  </el-col>
                </el-row>
                <el-row class="buttonWrapTA">
                  <el-col :span="4" class="buttonWrap ">
                    <el-button @click="exportList" class="upload" type="primary" round icon="iconfont icon-export" size="mini">导出
                    </el-button>
                  </el-col>
                </el-row>
              </div>

              <div class="statement">
                <el-table
                  @sort-change="sortChange"
                  ref="singleTable"
                  :data="mostInstancesList"
                  highlight-current-row
                  :height="isShow?fullHeight-270:fullHeight-363"
                  border
                  class="loadingtext tableWidth">
                  <el-table-column
                    prop="rowNumber"
                    align="center"
                    type="index"
                    width="100"
                    label="排名"
                    sortable='custom'>
                  </el-table-column>
                  <el-table-column
                    prop="activityName"
                    align="center"
                    property="processcall"
                    label="步骤名称">
                  </el-table-column>
                  <el-table-column
                    prop="approvalCnt"
                    align="center"
                    property="isValid"
                    label="处理流程实例数"
                    sortable='custom'>
                  </el-table-column>
                  <div slot="empty" v-show="!imgLoading">
                    <p :style="{'marginTop': '23px'}">暂无数据</p>
                  </div>
                  <div slot="append">
                    <page :pageIndex="pageIndex" :total="total" :pageSize="pageSize" :pageSizesList="pageSizesList"
                    @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></page>
                  </div>
                </el-table>
              </div>
              
            </div>

          </div>

        <!--</div>-->
        <!--弹窗组件-->
        <component :is="componentParam.componentType" v-if="showdialog"
                   @hideComponent="hideComponent" v-on:sureSubmit="showMsgfromchild"
                   v-bind:componentParam="componentParam"></component>
      </el-header>
    </el-container>
  </div>
</template>

<script>
  import processClassification from '../components/dailog/processClassification.vue'
  import processSelection from '../components/dailog/processSelection.vue'
  import moment from 'moment';
  import util from '../js/util';
  import Format from '../js/format';
  import loading from '../plugins/loading/loading'
  import page from "../plugins/page/index";


  /*本周*/
  var firstDay = moment().day(0).format('YYYY-MM-DD');
  var lastDay = moment().day(6).format('YYYY-MM-DD');

  /*本月*/
  var firstMonth = moment().startOf('month').format('YYYY-MM-DD');
  var lastMonth = moment().endOf('month').format('YYYY-MM-DD');

  /*本季度*/

  var firstSeason = moment().startOf('quarter').format('YYYY-MM-DD');
  var lastSeason = moment().endOf('quarter').format('YYYY-MM-DD');

  /*本年*/

  var firstYear = moment().startOf('year').format('YYYY-MM-DD');
  var lastYear = moment().endOf('year').format('YYYY-MM-DD');
  export default {
    name: 'reportsInstance',
    components:{
      'processClassification': processClassification, //选择流程分类
      'processSelection':processSelection,             //选择流程
      'page': page
    },
    /**
     * 以"岗位"统计处理实例最多界面构造函数
     */
    mounted() {
      // 获取列表数据
      //this.getList();
    },
    data() {
      return {
        processCategoryName:'',//流程分类名
        processCategoryIDs:'',//流程分类id
        processCategoryList:[],//流程分类选中数据
        processName:'',// 流程名
        processIDs:'',//流程id
        processList:[],//流程选中数据
        fullHeight: document.documentElement.clientHeight,
        pickerOptions2: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },   {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        alternativeGroup: '',

        // 查询条件
        model: {
          date:'' //选择时间
        },
        childDataUrl:{url:''},//传给子组件的值
        showdialog: false,//控制弹窗打开关闭
        componentParam:{
          //弹窗类型
          componentType:{
            type: String,
            required: true
          },
          //需要 回填到组件上的 数据集合
          backfillData:{
            type: Array,
            required: true
          },
          //子组件是否单选(true单选，false不是单选)
          oneCheck:{
            type: Boolean,
            required: true
          },
          //控制弹窗头部标题显示隐藏(默认显示)
          closeShow:{
            type: Boolean,
            required: true
          },
        },
        comp: {},
        showList: false,//列表显示状态
        isShow:true,
        startDate: firstDay,//开始日期
        endDate: lastDay,//结束日期
        type: 1,
        isShowTwo:true,
        arrtTwo:'收起',
        iconTwo: 'el-icon-arrow-down',
        selectValue: 1,
        // 分页参数
        pageIndex: 1,
        total: 0,
        pageSize: 10,
        pageSizesList: [10,20,50,100],
        // table数据
        mostInstancesList: [],
        orderBy:'', //排序
        exportData:[],
        start:'',
        end:'',
        // 加载动画
        imgLoading: false
      }
    },
    methods:{
      //表格排序
      sortChange(column){
        this.orderBy = `${column.prop} ${column.order.replace('ending','')}`;
        this.getList();
      },
      showToggleTwo(){
        var that=this;
        that.isShow = !that.isShow;
        if(that.isShow){
          that.arrtTwo="收起"
          that.iconTwo = "el-icon-arrow-up"
        }else {
          that.arrtTwo="展开"
          that.iconTwo = "el-icon-arrow-down"
        }
      },
      /**
       * 查询方法
       */
      search() {
        this.pageIndex = 1;
        this.getList();
      },
      /**
       * 重置方法
       */
      reset() {
        this.componentParam.backfillData = new Array();
        this.processList = new Array();
        this.processCategoryList = new Array();
        this.$nextTick(() => {
          this.selectValue = 1;
          this.startDate = firstDay;
          this.endDate = lastDay;
          this.model.date = '';//日期
          this.model.companys = '';
          this.processName = '';
          this.processIDs='';
          this.processCategoryName='';
          this.processCategoryIDs='';//流程分类id
        });
      },
      //-------------------------------      弹窗
      /**
       * 打开弹出框组件
       */
      openDailog(type) {
        //控制弹窗头部标题显示隐藏(默认显示)
        this.componentParam.closeShow = false;
        // 弹出框类型
        this.componentParam.componentType = type;
        //是否单选
        this.componentParam.oneCheck = false;
        //设置回填数据
        if(type == 'processClassification'){
          this.componentParam.backfillData = this.processCategoryList;
        }else  if(type == 'processSelection'){
          this.componentParam.backfillData = this.processList;
        }
        this.showdialog = true;
        util.showDailog_mask();//解决ie浏览器问题显示遮罩
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },
      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
         //流程分类 选择
        if (data.type && data.type == "processClassification") {
          this.processCategoryList= data.list;
          let  processCategoryIDs = [];
          let  processCategoryName = [];
          for(let node of data.list){
            processCategoryName.push(node.name);
            processCategoryIDs.push(node.id+'');
          }
          this.processCategoryIDs = processCategoryIDs.join(',');
          this.processCategoryName = processCategoryName.join('；');
        }else if(data.type && data.type == "processSelection"){
          //流程 选择
          this.processList = data.list;
          let processId = [];
          let processName = [];
          for(let node of data.list){
            processName.push(node.name);
            processId.push(node.processId+'');
          }
          this.processIDs = processId.join(',');
          this.processName = processName.join('；');
        }
        this.hideComponent();
      },
      change(){
        switch (this.selectValue) {
          case 1:
            this.startDate = firstDay;
            this.endDate = lastDay;
            break;
          case 2:
            this.startDate = firstMonth;
            this.endDate = lastMonth;
            break;
          case 3:
            this.startDate = firstSeason;
            this.endDate = lastSeason;
            break;
          case 4:
            this.startDate = firstYear;
            this.endDate = lastYear;
            break;
          case 5:
            // this.startDate = this.start;
            // this.endDate = this.end ;
              this.startDate = this.model.date[0] ? `${this.model.date[0]} 00:00:00` : '';
              this.endDate = this.model.date[1] ? `${this.model.date[1]} 23:59:59` : '';
            break;
        }

      },

      /**
       * 获取列表数据
       */
      getList() {
        this.change();
        if (this.processIDs.length === 0 && this.processCategoryIDs.length === 0) {
          this.$message.warning('请选择流程分类或流程选择进行查询！');
          return;
        }
        var that = this;
        let data = {
          "pageIndex": this.pageIndex-1,
          "pageSize": this.pageSize,
          "startDate": this.startDate,
          "endDate": this.endDate,
          "processIDs":this.processIDs,
          "processCategoryIDs":this.processCategoryIDs,
          "orderBy":this.orderBy
        }
        // loading.startLoading();
        this.imgLoading = true;
        that.$axios.post('/api/MostInstances/page', data).then((result) => {
          if(result.status===200) {
            // 数据集合
            that.mostInstancesList = result.data.mostInstancesList;
            //总数
            that.total = result.data.total;
          }else{
            this.$message.error("请求失败！");
          }
          // loading.endLoading();
          this.imgLoading = false;
        }).catch((err) => {
          // loading.endLoading()
          this.imgLoading = false;
          that.$message.error(`连接服务器失败！${err}`);
        })
      },


      exportList() {
        this.change();
        loading.startLoading({
            lock: true,
            text: '数据量过大，正在拼命加载中...',
            background: 'rgba(255, 255, 255, 0.9)',
            target: document.querySelector('.loadingtext')
          });
        let obj={
          "pageIndex": 0,//this.pageIndex-1,
          "pageSize": 1000,//this.total,
          "startDate": this.startDate,
          "endDate": this.endDate,
          "processIDs":this.processIDs,
          "processCategoryIDs":this.processCategoryIDs,
          "orderBy":this.orderBy
        };
        this.$axios.post('/api/exportExcelForTable',  {'tableName':'动态-岗位/人-以“岗位”统计处理实例最多','params':obj}, {responseType: 'blob'}).then((result) => {
          loading.endLoading();
          const content = result.data;
          const dowLoadFileName = '动态-岗位/人-以“岗位”统计处理实例最多';
          util.exportSearchList(dowLoadFileName, content)

        }).catch((err) => {
          loading.endLoading();
          this.$message.error(`导出失败!ERROR MESSAGE:${err}`);
        })
      },


      /**
       * 页面size变更方法
       */
      handleSizeChange(val) {
        var pageSize = `${val}`;
        this.pageIndex = 1;
        this.pageSize = parseInt(pageSize);
        this.getList();
      },
      /**
       * 翻页方法
       */
      handleCurrentChange(val) {
        this.pageIndex = val;
        this.getList()
      },
      /**
       *设置表头标题class
       */
      tableHeaderClassName({row, rowIndex}) {
        return "table-head-th";
      },
    },
    watch: {
      fullHeight () {
        return document.documentElement.clientHeight;
      }
    },
    computed: {}
  }
</script>

<style lang="scss" scoped>
  .staticReports .el-header .staticReports{
    padding: 0px;
  }
  .staticReports .el-header .staticReports .staticReports-header{
    padding: 0px;
  }
  .staticReports .el-header .staticReports .taticReports-content{
    margin-top: 0px;
  }
  .reportInstancebtns{
    text-align: right;
    padding-right: 30px;
  }
  .staticReports .el-container > .el-header .el-row{
    height: 48px;
    line-height: 48px;
  }
  .staticReports .titleMT{
    margin-top: 0px !important;
     margin-left: 0px;
    font-size: 16px !important;
  }
  //开始
  .titleMT {
    /*<!--margin-top:-7px !important;-->*/
    /*<!--margin-left:8px;-->*/
    font-size: 16px !important;
    padding-left: 25px;
  }
  .staticReports .el-header .staticReports .taticReports-content .statement{
    padding-left: 25px;
  }
  .arrtFS {
    font-size: 12px;
  }
  .lh35 {
    padding-left: 1px !important;
  }

  /*流程分类选择*/
  .classSelect {
    min-width: 100px;
  }
  .classSelect>span {
    display: inline-block;
    padding-right:15px;
    text-align: right;

  }
  .classSelectMR {
    margin-right: 10px;
    position: relative;
    .icon-organization{
      position: absolute;
      top: 0;
      right: 2%;
      z-index: 1
    }
  }
  /*流程选择*/
  .processSelect {
    min-width: 78px;
  }
  .processSelect>span {
    display: inline-block;
    padding-right:15px;
    text-align: right;

  }
  .textIndent {
    text-indent: 6px;
  }
  /*时间段*/
  .timeDuan {
    min-width: 98px;
    padding-right:15px;
    text-align: left;
    margin-top: -10px;

  }


  .timeSelect {
    margin-right: 10px;
    margin-top: -12px;
  }

  .space {
    min-width: 82px;
  }
  .staticReports{
    .buttonWrapTA {
      text-align:right;
      height: 0px;
    }
  }

  .tableWidth {
    width: 100%;
  }
  .timeWidth {
    width: auto;
  }

  .fz16 {
    display:inline-block;
    margin-bottom: 20px;
  }
  .reportCondition-date.reportConditionDate {
    margin-top:0px !important;
  }
  .staticReports-middle.lh35.staticReportsMiddle{
    margin-bottom:0px !important;
  }

  .mz:hover{
    color: #1fa0fd;
    cursor: pointer;
  }
  //结束
</style>




